@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}

.Weather {
  margin-bottom: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Weather input {
  padding: 0.4em 0.7em;
  margin-top: 10px;
  border: 1px solid lightgray;
  font-size: 1.1rem;
}

.wet {
  display: flex;
  align-items: center;
  gap: 24px;
}

.wet img {
  border: 2px solid lightgray;
  border-radius: 50%;
  width: 60px;
  height: 60px;
}

.wet-img {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.wet-desc {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@keyframes rotate {
  from {
    transform: rotate(-180deg);
  }
  to {
    transform: rotate(0deg);
  }
}

.rotate {
  margin-top: 7px;
  color: gray;
  cursor: pointer;
}

.rotate:active {
  animation: rotate 200ms;
}

.Todo {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  gap: 20px;
  justify-content: center;
  align-items: center;
}

.todo-container {
  width: max(300px, 30vw);
  max-height: 72vh;
  box-shadow: 1px 4px 6px rgba(0, 0, 0, 0.4);
}

.todo-container form {
  position: relative;
  width: 100%;
  display: flex;
  align-items: center;
  height: 50px;
  gap: 0;
}

.today-date {
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid #d5d5d5;
  align-items: center;
  padding-inline: 25px 20px;
  color: #444;
}

.datess {
  display: grid;
  grid-template-columns: 35px 20px;
  justify-items: center;
  align-items: center;
  gap: 0;
  border: 0;
}

.datess label {
  font-size: 0.8rem;
}

.datess .day {
  font-family: "Poppins", sans-serif;
  font-size: 1.8rem;
  grid-row: 1/3;
  margin-right: 16px;
}

.todo-container input {
  font-size: 1.1rem;
  width: 100%;
  margin-inline: auto;
  padding: 0.7em 30px 0.7em 52px;
  border: 0;
}

.todo-container .fa-arrow-down {
  position: absolute;
  color: #999;
  left: 30px;
}

.todo-container input::placeholder {
  font-size: 1.1rem;
  color: #999;
}

.Tod {
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #d5d5d5;
  padding: 10px;
  transition: all 150ms ease-in-out;
  margin: 0;
}

.Tod:hover {
  transition: all 350ms ease-in-out;
  background-color: #f5f5f5;
}

.todo-list {
  height: calc(100% - 130px);
  overflow-y: auto;
}

.Tod .todo-label {
  color: #222;
  padding-inline: 20px;
}

.Tod .todo-label.comp {
  text-decoration: line-through;
  color: #888;
}

.Tod span {
  display: flex;
  gap: 5px;
}

.Tod span i {
  cursor: pointer;
  font-weight: 100;
  font-size: 18px;
}

.Tod .fa-close {
  opacity: 0;
  transition: all 200ms ease-in-out;
  cursor: none;
}

.Tod:hover .fa-close {
  opacity: 1;
  transition: all 250ms ease-in-out;
  cursor: pointer;
  color: #444;
}

.Tod .fa-close:hover {
  color: rgb(229, 44, 44);
}

.otherss {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 30px;
  border-top: 1px solid #d5d5d5;
  font-size: 0.7rem;
  color: #555;
  padding-inline: 30px 20px;
}

.otherss button {
  border: 1px solid #d5d5d5;
  padding: 0.25em 0.5em;
  font-size: 0.7rem;
  background-color: #fff;
  cursor: pointer;
  color: #555;
  transition: all 200ms ease-in-out;
}

.otherss button:hover {
  background-color: #d5d5d5;
  color: white;
  transition: all 300ms ease-in-out;
}
